/*  BUTTON EFFECTS */

.fullWidthButton {
  transition-property: height;
  transition-duration: 0.5s;
  transition-timing-function: ease;
  outline: none;
}

.label {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
  transition: opacity 0.5s ease;
  opacity: 0;
  /* Hidden initially */
}

.label.fade-in {
  opacity: 1;
  /* Fully visible */
}

.label.fade-out {
  opacity: 0;
  /* Hidden */
}

.fullWidthButton.expanded {
  position: relative;
  /* Expanded height */
}

/* Transition types */
.fullWidthButton.ease {
  transition-timing-function: ease;
}

.fullWidthButton.ease-in {
  transition-timing-function: ease-in;
}

.fullWidthButton.ease-out {
  transition-timing-function: ease-out;
}

.fullWidthButton.linear {
  transition-timing-function: linear;
}

/* Bounce animation */
@keyframes bounce {

  0%,
  100% {
    transform: scaleY(1);
  }

  50% {
    transform: scaleY(1.1);
  }
}

.fullWidthButton.bounce {
  animation: bounce 0.5s ease-in-out;
}

