.gps-circle {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #4caf50;
  position: relative;
  display: flex;
  /* Ensures it's centered if internal elements are added later */
  justify-content: center;
  align-items: center;
}

.gps-circle.pulsing::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: rgba(76, 175, 80, 0.4);
  animation: pulse 2s infinite;
}

.gps-circle.success {
  background-color: #4caf50;
}

.gps-text {
  color: #00FF00;
}

@keyframes pulse {
  0% {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }

  100% {
    transform: translate(-50%, -50%) scale(3);
    opacity: 0;
  }
}


